<template>
    <div class="page-header" :class="`${$store.state.menuShow ? 'show' : ''}`">
        <div class="header-content">
            <div class="logo" v-if="$store.state.menuShow">
                <router-link to="/"></router-link>
            </div>
            <div class="right">
                <wallet v-show="$store.state.menuShow" />
                <div class="menu" @click="showMenu">
                    <i v-if="$store.state.menuShow" class="icon icon-collect" />
                    <i v-else class="icon icon-menu" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import wallet from './wallet'
import {local} from '../../config/constant'
export default {
    name: 'HEADER',
    components: {
        wallet
    },
    data() {
        return {
            open: false,
            showMenuTime: 0
        }
    },
    computed: {
        cpd_lang() {
            if (this.$i18n.locale === 'en-US') {
                return '中文'
            } else {
                return 'English'
            }
        }
    },
    methods: {
        toggleLang() {
            let v = this.$store.state.lang
            if (v === 'zh-CN') {
                v = 'en-US'
            } else {
                v = 'zh-CN'
            }
            this.$store.commit('setLang', v)
            this.$i18n.locale = v
            localStorage.setItem(local.LOCALE, v)
        },
        showMenu() {
            const time = new Date().getTime()
            if (time - this.showMenuTime > 1000 * 0.3) {
                this.showMenuTime = time
                this.$store.commit('setMenuShow', !this.$store.state.menuShow)
            }
        }
    }
}
</script>

