import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        account: '',
        chainId: '',
        lang: 'en-US',
        menuShow: false,
        theme: localStorage.getItem('theme') || 'dark',
        visibleWalletModal: false,
        totalValueLocked: 0,
        mncMarketCap: 0
    },
    mutations: {
        setAccount(state, value) {
            state.account = value
        },
        setChainId(state, value) {
            state.chainId = value
        },
        setLang(state, value) {
            state.lang = value
        },
        setMenuShow(state, value) {
            state.menuShow = value
        },
        setTheme(state, value) {
            state.theme = value
            localStorage.setItem('theme', value)
        },
        setVisibleWalletModal(state, value) {
            state.visibleWalletModal = value
        },
        setTotalValueLocked(state, value) {
            state.totalValueLocked = value
        },
        setMncMarketCap(state, value) {
            state.mncMarketCap = value
        }
    },
    modules: {}
})

export default store
