import Web3 from 'web3'
import Web3Modal from "web3modal"
import { bufferToHex } from 'ethereumjs-util'
import {netWork, SUPPORTED_WALLETS} from '../config/constant'

const { ethereum } = window

if ( ethereum ) {
  ethereum.autoRefreshOnNetworkChange = false
}

const queue = {

}

class VueWeb3 {
  constructor(store) {
    if (!ethereum) {
      this.web3Env = false
      return
    } else {
      this.web3Env = true
    }
    const providerOptions = this.getOptions()
    const opts = {
      cacheProvider: true,
      providerOptions
    }
    this.$store = store
    this.web3Modal = new Web3Modal(opts)
    if (ethereum) {
      this.constructor.prototype.provider = ethereum
      const web3 = new Web3(ethereum)
      this.web3 = web3
      this.constructor.prototype.web3 = web3
      console.log(ethereum, 'ethereum')
      let chainId = ethereum.chainId
      if (ethereum.isTrust) {
        chainId = web3.utils.toHex(chainId)
      }
      this.$store.commit('setChainId', chainId)
    }
  }

  getOptions() {
    const providerOptions = {}
    Object.values(SUPPORTED_WALLETS).forEach(item => {
      providerOptions[item.id] = {
        package: item.connector
      }

      if (item.options) {
        providerOptions[item.id].options = item.options
      }
      if (item.type === 'custom') {
        providerOptions[item.id].display = item.display
        providerOptions[item.id].connector = async (ProviderPackage, options) => {
          const provider = new ProviderPackage(options)
          await provider.enable()
          return provider
        }
      }
    })
    return providerOptions
  }

  on(type='chainChanged', listener = () => {}) {
    const item = {
      count: Infinity,
      listener
    }
    if (queue[type]) {
      queue[type].push(item)
    } else {
      queue[type] = [item]
    }
  }

  getQueue() {
    return queue
  }

  once(type='chainChanged', listener = () => {}) {
    const item = {
      count: 1,
      listener
    }
    if (queue[type]) {
      queue[type].push(item)
    } else {
      queue[type] = [item]
    }
  }

  emit(type='chainChanged') {
    const queueListeners = queue[type]
    const quoteObject = {}
    const deleteListeners = []
    if (queueListeners) {
      queueListeners.forEach((item, index) => {
        quoteObject[index] = item
        if (item.count === 1) {
          deleteListeners.push(index)
        }
        item.listener && item.listener()
      })
    }
    deleteListeners.forEach(item => {
      delete quoteObject[item]
    })
    queue[type] = Object.values(quoteObject)
  }

  onConnect(e) {
    console.log('connect', e)
  }

  onDisconnect(e) {
    console.log('disconnect', e)
  }

  onChainChanged(chainId) {
    console.log('chainChanged', chainId)
    this.$store.commit('setChainId', chainId)
    this.emit('chainChanged')
  }

  onClose(e) {
    if (e === 1000) {
      this.$store.commit('setAccount', '')
    }
    console.log('close', e)
  }

  onAccountsChanged(accounts) {
    this.$store.commit('setAccount', accounts[0])
    console.log('accountsChanged', accounts)
  }

  personalSign = (account) => {
    const msg = bufferToHex(new Buffer(account, 'utf8'))
    return this.provider.result({
      method: 'personal_sign',
      params: [
        msg,
        account
      ],
      form: account
    })
  }

  async connectWallet(id) {
    console.log(this.web3Modal, 'this.web3Modal')
    const provider = await this.web3Modal.connectTo(id)
    this.setProvider(provider)
    const web3 = new Web3(provider)
    const accounts = await web3.eth.getAccounts()
    this.$store.commit('setAccount', accounts[0])
    const chainId = await web3.eth.getChainId()
    this.$store.commit('setChainId', web3.utils.toHex(chainId))
  }
  //
  async wallet_addEthereumChain(chainName) {
    let addEthereumChain = 'fail'
    const options = netWork[chainName]
    try {
      const params = {
        method: 'wallet_addEthereumChain',
        params: [options]
      }
      addEthereumChain = await this.provider.request(params)
      if (this.web3Modal.cachedProvider) {
        await this.connectWallet(this.web3Modal.cachedProvider)
      }
    } catch (addError) {
      console.log(addError, 'addError')
    }
    return addEthereumChain
  }
  async wallet_switchEthereumChain(chainName) {
    let switched = 'fail'
    try {
      switched = await this.provider.request({
        method: 'wallet_switchEthereumChain',
        params: [{
          chainId: netWork[chainName].chainId
        }]
      })
    } catch (e) {
      switched = await this.wallet_addEthereumChain(chainName)
    }
    return switched
  }

  //
  async getAccount() {
    const account = await this.provider.request({
      method: 'eth_requestAccounts'
    })
    return  account
  }
  // 交易
  sendTransaction(method, params) {
    return ethereum.request({
      method: method || 'eth_sendTransaction',
      params: params || []
    })
  }
  // 向钱包添加代币
  walletWatchAsset() {
    return this.on('wallet_watchAsset', {
      params: {
        type: 'ERC20', // Initially only supports ERC20, but eventually more!
        options: {
          address: 'tokenAddress', // The address that the token is at.
          symbol: 'tokenSymbol', // A ticker symbol or shorthand, up to 5 chars.
          decimals: 'tokenDecimals', // The number of decimals in the token
          image: 'tokenImage', // A string url of the token logo
        },
      }
    })
  }

  setProvider(provider) {
    this.constructor.prototype.provider = provider
    this.constructor.prototype.web3 = new Web3(provider)
    this.provider.on('connect', (e) => {
      this.onConnect(e)
    })
    this.provider.on('disconnect', (e) => {
      this.onDisconnect(e)
    })
    this.provider.on('chainChanged', (chainId) => {
      this.onChainChanged(chainId)
    })
    this.provider.on('accountsChanged', (accounts) => {
      this.onAccountsChanged(accounts)
    })
    this.provider.on('close', (e) => {
      this.onClose(e)
    })
  }
  contract = (abi, address, options) => {
    const contract = new this.web3.eth.Contract(abi, address, options)
    return contract
  }
}

export default function install(Vue, { store }) {
  const vueweb3 = new VueWeb3(store)
  Vue.prototype.$vueweb3 = vueweb3
  Vue.prototype.$ethereum = vueweb3.provider || window.ethereum
  Vue.prototype.$web3 = new Web3(Vue.prototype.$ethereum)
}
