<template>
    <div class="component-wallet">
        <div v-if="!cpd_account" class="wallet-button" @click="openWalletModal">
            <b>{{ text }}</b>
        </div>
        <div class="connected" v-else @click="visible = true">
            <div class="wallet-icon"><i class="icon icon-wallet" /></div>
            <b>{{ cpd_account ? cpd_accountText : text }}</b>
            <div class="arrow"><van-icon name="arrow-down" /></div>
        </div>
        <ui-dialog
            v-model="$store.state.visibleWalletModal"
            title="Connect to a Wallet"
            @close="onWalletModalClose"
        >
            <div class="wallet-options">
                <div class="option-item" v-for="(item, index) in options" :key="index" @click="connectWallet(item.id)">
                    <div class="name">{{ item.name }}</div>
                    <div class="icon"><img :src="item.icon" /></div>
                </div>
            </div>
        </ui-dialog>
        <van-dialog
            v-model="visible"
            title="Your Wallet"
            :closeOnClickOverlay="true"
            :show-confirm-button="false"
            @close="onYourWalletClose"
        >
            <div class="your-wallet-content">
                <div class="your-address">
                    <label>Your Wallet</label>
                    <div class="address">
                        <span style="font-family: GothamBold;">{{ cpd_account }}</span>
                        <div class="copy-button"><i class="icon icon-copy" /></div>
                    </div>
                </div>
                <div class="your-balance">
                    <div class="item">
                        <label>ETH Balance</label>
                        <span>{{balance}}</span>
                    </div>
                </div>
<!--                <div class="view-exp">-->
<!--                    <span>View on EtherScan</span>-->
<!--                    <i class="icon icon-link" />-->
<!--                </div>-->
                <div class="disconnect-button">
                    <van-button>Disconnect Wallet</van-button>
                </div>
            </div>
        </van-dialog>
    </div>
</template>

<script>
import {SUPPORTED_WALLETS, ChainName} from '../../config/constant'

const swicthChainObject = process.env.VUE_APP_NODE_ENV === 'production' ? {
    '0x1': true
} : {
    '0x5': true
}

export default {
    name: 'WALLET',
    props: {
        text: {
            type: String,
            default: 'Connect Wallet'
        }
    },
    data() {
        return {
            visible: false,
            options: Object.values(SUPPORTED_WALLETS),
            switchChainLoading: false,
            web3Modal: null,
            currentChain: false,
            balance: 0
        }
    },
    computed: {
        cpd_account() {
            return this.$store.state.account
        },
        cpd_accountText() {
            const account = this.cpd_account
            if (!account) {
                return ''
            }
            const len = account.length
            const arr = account.split('')
            arr.splice(2, len - 6, '...')
            return arr.join('')
        },
        cpd_currentChain() {
            if (!window.ethereum) {
                return this.currentChain
            }
            return swicthChainObject[this.$store.state.chainId]
        },
        cpd_swicthChainName() {
            let chainName = ''
            for (let key in swicthChainObject) {
                if (swicthChainObject[key] !== this.cpd_currentChain) {
                    chainName = ChainName[key]
                }
            }
            return chainName
        }
    },
    watch: {
        cpd_account(v) {
            if (!v) {
                this.$vueweb3.web3Modal.clearCachedProvider()
            } else {
                if (window.postMessageInited) {
                    console.log(this.cpd_account)
                    window.parent.postMessage({
                        method: 'getAccount',
                        payload: this.cpd_account
                    }, '*')
                }
                this.getBalance(v)
                this.onWalletModalClose()
            }
        }
        // '$store.state.visibleWalletModal'(v) {
        //     this.visible = v
        // }
    },
    mounted() {
        const currentProvider = this.$web3.currentProvider
        if (currentProvider) {
            if (!swicthChainObject[this.$store.state.chainId]) {
                this.switchChain()
            } else {
                if (this.$vueweb3.web3Modal.cachedProvider) {
                    this.connectWallet(this.$vueweb3.web3Modal.cachedProvider)
                }
            }
        }
    },
    methods: {
        openWalletModal() {
            this.$store.commit('setVisibleWalletModal', true)
        },
        async connectWallet(id) {
            console.log(this.$vueweb3.web3, 'this.$vuweb3')
            await this.$vueweb3.connectWallet(id)
        },
        async getBalance(address) {
            const balance = await this.$web3.eth.getBalance(address)
            this.balance = this.$BN.formatValue(this.$web3.utils.fromWei(balance, 'ether'), 4)
        },
        async switchChain() {
            if (!window.ethereum) {
                if (this.$store.state.account) {
                    this.currentChain = !this.currentChain
                }
                return
            }
            this.switchChainLoading = true
            const switchChained = await this.$vueweb3.wallet_addEthereumChain(this.cpd_swicthChainName)
            // console.log(switchChained, 8888888)
            if (switchChained !== 'fail') {
                this.switchChainLoading = false
                // console.log(this.$store.state.account, 9999)
                if (!this.$store.state.account) {
                    let chainId = ''
                    // console.log(chainId, 888)
                    for (let key in swicthChainObject) {
                        if (key !== this.$store.state.chainId) {
                            chainId = key
                        }
                    }
                    // console.log(chainId, 7777)
                    this.$store.commit('setChainId', chainId)
                }
            }
        },
        onWalletModalClose() {
            this.$store.commit('setVisibleWalletModal', false)
        },
        onYourWalletClose() {
            this.visible = false
        }
    }
}
</script>

