import BigNumber from 'bignumber.js'
BigNumber.config({ EXPONENTIAL_AT: 1e+9 })

class BigNum {
  constructor(num) {
    this.value = new BigNumber(num)
  }
  getValue() {
    return this.value
  }
  static formatValue(val, decimal) {
    const value = new BigNumber(val)
    return  value.toFixed(decimal, 1)
  }
}

export default function install(Vue, { store }) {
  Vue.prototype.$BN = BigNum
  Vue.prototype.$bigNumber = (num) => {
    return new BigNum(num)
  }
}
