import './vant'
const files = require.context('.', false, /\.js$/)
export default function installPlugins(Vue, {store, router, i18n}) {
    files.keys().forEach((key) => {
        if (key !== './index.js' && key !== './vant.js') {
            const file = files(key).default
            Vue.use(file, {store, router, i18n})
        }
    })
}
